@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@300;400;700&display=swap);
body{background-color:azure;font-family:"Josefin Sans", sans-serif;font-weight:400}.todo-list__title{text-align:center}.todo-list__item{width:800px}.todo-list__item--firstline{font-weight:700}.container{box-sizing:border-box;display:flex;align-items:center;justify-content:space-between;text-align:center;width:800px;height:40px;margin:auto;border:2px solid #888;border-radius:5px;background-color:beige}

.todoList{margin:0 auto;padding:0;width:100%}.todoList__item{box-sizing:border-box;display:flex;align-items:center;justify-content:space-between;text-align:center;width:800px;min-height:50px;margin:5px auto;border:2px solid tan;border-radius:5px;background-color:rgba(255,235,205,0.3);transition:0.3s}.todoList__item:hover{-webkit-transform:scale(101%);transform:scale(101%);background-color:rgba(255,235,205,0.8)}.todoList__item--completed{background-color:rgba(144,238,144,0.3)}.todoList__item--completed:hover{background-color:rgba(144,238,144,0.8)}.todoList__userInfo{display:block;width:100%}

